* {
  margin: 0px;
  padding: 0px;
}

body {
  background-color: rgb(255, 255, 255);
  background-image: url('./images/SVG\ Left\ background.svg');
  background-attachment: fixed;
  background-size: cover;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: calc(12px);
  color: rgb(233, 231, 231);
}

#standard-multiline-static {
  color:whitesmoke;
}

#wrapper {
  display: flex;
  justify-content: center;
  width: 90vw;
}

#input-container {
  padding: 15px;
  width: 30vw;
  height: calc(100vh - 30px);
}

#output-container {
  margin-top: 15px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: rgba(245, 245, 245, 0.815);
  border: 5px solid navy;
  width: 45vw;
  height: calc(95vh - 30px);
  border-radius: 5%;
}

#credits {
  background-image: url('./images/SVG\ Left\ background.svg');
  background-attachment: fixed;
  background-size: cover;
  position: fixed;
  top: 0;
  left: 0;
  margin: 15px;
  width: 8vw;
  border-radius: 5%;
  color: whitesmoke;
  padding: 5px;
}

a {
  color: rgb(241, 202, 202);
}

/* Markdown styling choices */

li {
  color: rgb(78, 78, 167);
  margin-left: 15px;
}

em {
  color: rgb(135, 145, 170);
}

h1 {
  color: rgb(175, 149, 1)
}

h2 {
  color: goldenrod
}

p {
  color: rgb(165, 165, 31);
}